.footerRelative {
  position: relative;
  background-color: #f6f6f6;
  border-top: 1px solid #e3e6f0;
}

.footerFixed {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #f6f6f6;
  border-top: 1px solid #e3e6f0;
}

.footerContent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.footerNavigation {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: center;
      justify-content: center;
}

/* NavigationFooter */
.container {
  width: 160px;
}

.header {
  text-decoration: underline;
  margin: .5em 0em;
}

.itemList {
  list-style: none;
  padding: 0px;
  margin: 0px;
}

.item {
  padding: 5px 0px 5px 2px;
}

.link {
  text-decoration: none;
  color: black;
}

.link:active, .link:focus, .link:visited {
  color: black;
}

/* Footer Details */
.detailsContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.footerCopyRight {
  text-align: center;
}


